<script>
import Paper, { Title, Content } from '@smui/paper';
import Button, { Label } from '@smui/button';
import { onMount } from 'svelte';

let service = window['services'];
let todoService = (service)? service.todoManagerService : undefined;

let todos = (localStorage.getItem("deletedTodos"))? JSON.parse(localStorage.getItem("deletedTodos")) : [];

function add(name) {
    todos.push({
    todo: name
    });
    todos = todos;
    localStorage.setItem("deletedTodos", JSON.stringify(todos));
}

function recover(name) {
    if(todoService != undefined) todoService.addedObservable().next(name);
    for(var i = 0; i<todos.length; i++) {
        if(todos[i].todo == name) {
            todos.splice(i, 1);
        }
    }
    todos = todos;
    localStorage.setItem("deletedTodos", JSON.stringify(todos));
}

onMount(() => {
    if(todoService != undefined){
        var deletedObserver = todoService.deletedObservable().subscribe((data)=>{
            add(data);
        });
    }
});
</script>

<svelte:options tag="todo-recovery" />
<main>
    <Paper color="primary">
        <Title>To-do recovery list</Title>
        <Content>
            {#each todos as t}
            <div class="todo-wrapper completed-{t.completed}">
                <Paper>
                    <div class="todo-content">
                        <div class="todo-title">
                            <p>{t.todo}</p>
                        </div>
                        <Button color="primary" on:click={() => recover(t.todo)} variant="raised">
                            <Label>Recover</Label>
                        </Button>
                    </div>
                </Paper>
            </div>    
            {/each}
        </Content>
    </Paper>
</main>

<style scoped>
@import url('https://cdn.jsdelivr.net/npm/svelte-material-ui/bare.min.css');

main{
    margin: auto;
    width: fit-content;
