    --mdc-theme-primary: var(--theme-secondary, #eb9486);
    --mdc-theme-on-primary: var(--theme-on-secondary, #000);
}

.todo-title{
    display: flex;
    align-items: center;
}

p{
    font-size:1.25rem;
    margin:0px;
}
</style>